import { useEffect, useRef } from 'react';
import { notification } from './index';
import { put } from 'redux-saga/effects';
import { RESPONSE_STATUS } from '@app-utils/Constants';
import { split, toLower } from 'lodash';

export function usePrevious(value) {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
}

export function* standardErrorRes(error, errorText, callback) {
  notification({
    type: 'error',
    text: errorText,
  });
  if (error.response) {
    switch (error.response.status) {
      case RESPONSE_STATUS.UNAUTHORIZED:
        break;
      default:
        yield put(callback(error.response));
        break;
    }
  } else {
    yield put(callback(error.response));
  }
}

const initialName = (name) => {
  name = name == null ? '-' : name; // if data null, replace with -
  const initials = name.match(/\b\w/g) || [];
  const initial = (
    (initials.shift() || '') + (initials.pop() || '')
  ).toUpperCase();
  return initial;
};

export { initialName };

const formatBytes = (bytes, decimals = 2) => {
  if (bytes === 0) return '0 Bytes';

  const k = 1024;
  const dm = decimals < 0 ? 0 : decimals;
  const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];

  const i = Math.floor(Math.log(bytes) / Math.log(k));

  return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
};

export { formatBytes };

const isEmpty = (something) => {
  if (something === '' || something === undefined || something === null) {
    return true;
  }
  return false;
};

export { isEmpty };

const checkImageDimension = (file, platformImageMaxDimension) => {
  let _URL = window.URL || window.webkitURL;
  let img = new Image();
  var objectUrl = _URL.createObjectURL(file);
  // console.log(objectUrl);

  img.onload = function () {
    let width = img.naturalWidth,
      height = img.naturalHeight;
    // console.log(platformImageMaxDimension[0]);
    // console.log(platformImageMaxDimension[1]);
    if (
      width !== platformImageMaxDimension[0] &&
      height !== platformImageMaxDimension[1]
    ) {
      // console.log('false wh : ' + width + " x " + height);
      return false;
    }
    // console.log('true wh : ' + width + " x " + height);

    _URL.revokeObjectURL(objectUrl);
  };

  // img.src = objectUrl;

  return true;
};

export { checkImageDimension };

const getImageDimension = (file) => {
  let _URL = window.URL || window.webkitURL;
  let img = new Image();
  var objectUrl = _URL.createObjectURL(file);

  img.onload = function () {
    let width = img.naturalWidth,
      height = img.naturalHeight;
    console.log('true wh : ' + width + ' x ' + height);

    return [width, height];
  };
  _URL.revokeObjectURL(objectUrl);

  return false;
};

export { getImageDimension };

function isDegree(word) {
  const PRESERVED_CAPITAL_LIST = {};
  return PRESERVED_CAPITAL_LIST[word] != null;
}

function convertToCapitalCase(string, preserveCapitalizedWord = true) {
  try {
    const wordArr = split(toLower(string), ' ');
    const capitalizedWordArr = [];

    wordArr.forEach((item) => {
      const convertedToString = item.toString();

      if (convertedToString !== '' && convertedToString != null) {
        let newWord;
        if (!isDegree(convertedToString)) {
          newWord = preserveCapitalizedWord
            ? `${convertedToString[0].toUpperCase()}${convertedToString.substring(
                1,
                item.length
              )}`
            : `${convertedToString[0].toUpperCase()}${convertedToString
                .substring(1, item.length)
                .toLowerCase()}`;
        } else {
          newWord = convertedToString;
        }
        capitalizedWordArr.push(newWord);
      }
    });

    return capitalizedWordArr.join(' ');
  } catch (e) {
    return string;
  }
}

export { convertToCapitalCase };
