import React, { useEffect } from 'react';
import PropTypes from 'prop-types';

export function Bootstrap(props) {
  function onRegister() {}

  useEffect(() => {
    onRegister();
  }, []);

  const { children } = props;

  return <>{children}</>;
}

Bootstrap.propTypes = {
  children: PropTypes.node,
};

export default Bootstrap;
