import { toast, Slide } from 'react-toastify';

export const notification = ({ type, text, delay = 2000 }) => {
  if (type === 'success') {
    toast.success(text, {
      containerId: 'B',
      transition: Slide,
      autoClose: delay,
    });
  }
  if (type === 'error') {
    toast.error(text, {
      containerId: 'B',
      transition: Slide,
      autoClose: delay,
    });
  }
  if (type === 'warning') {
    toast.warning(text, {
      containerId: 'B',
      transition: Slide,
      autoClose: delay,
    });
  }
};
