import { takeEvery, put, call, takeLatest } from 'redux-saga/effects';
import { notification } from '@app-utils/NotificationToast';
// Login Redux States
import { LOGIN_USER, LOGOUT_USER } from './actionTypes';
import {
  loginSuccess,
  logoutUserSuccess,
  apiError,
  loginReset,
} from './actions';
import { RESPONSE_STATUS } from '@app-utils/Constants';
import { postLoginServices } from './services';

function* loginUser({ payload: { user, history } }) {
  try {
    const { username, password } = user;
    const response = yield call(postLoginServices, { username, password });
    switch (response.status.code) {
      case RESPONSE_STATUS.SUCCESS:
        yield put(loginSuccess(response));
        notification({
          type: 'success',
          text: 'Berhasil login',
        });
        history.push('/dashboard');
        break;
      case RESPONSE_STATUS.BAD_REQUEST:
        notification({
          type: 'error',
          text: 'username dan password tidak sesuai',
        });
        yield put(apiError(response));
        break;
      case RESPONSE_STATUS.NOT_FOUND:
        notification({
          type: 'error',
          text: 'username dan password tidak sesuai',
        });
        yield put(apiError(response));
        break;
      case RESPONSE_STATUS.ERROR:
        notification({
          type: 'error',
          text: 'username dan password tidak sesuai',
        });
        yield put(apiError(response));
        break;
      default:
        break;
    }
  } catch (error) {
    notification({
      type: 'error euy',
      text: error.message,
    });
    yield put(apiError({ error: error.message }));
  } finally {
    yield put(loginReset());
  }
}

function* logoutUser({ payload: { history } }) {
  try {
    yield put(logoutUserSuccess());
    history.push('/signin');
  } catch (error) {
    yield put(apiError(error));
  }
}

export default [
  takeLatest(LOGIN_USER, loginUser),
  takeEvery(LOGOUT_USER, logoutUser),
];
