import {
  LOGIN_USER,
  LOGIN_SUCCESS,
  LOGOUT_USER,
  LOGOUT_USER_SUCCESS,
  API_ERROR,
  LOGIN_RESET,
} from './actionTypes';
import { cloneDeep } from 'lodash';
const initialState = {
  error: '',
  loading: false,
  isLoggedIn: false,
  isFailedLogin: false,
  action: '',
  sessionUser: {
    accessToken: '',
    refreshToken: '',
    expired_at: '',
  },
  email: '',
};

const login = (state = initialState, action) => {
  const { payload, type } = action;
  console.log(payload);
  const actions = {
    [LOGIN_RESET]: () => ({
      ...state,
      loading: false,
      action: type,
    }),
    [LOGIN_USER]: () => ({
      ...state,
      loading: true,
      action: type,
      email: payload.user.email,
    }),
    [LOGIN_SUCCESS]: () => ({
      ...state,
      loading: false,
      isLoggedIn: true,
      sessionUser: payload,
      action: type,
    }),
    [LOGOUT_USER]: () => ({
      ...state,
      action: type,
    }),
    [LOGOUT_USER_SUCCESS]: () => ({
      ...state,
      ...cloneDeep(initialState),
      action: type,
      isLoggedIn: false,
    }),
    [API_ERROR]: () => ({
      ...state,
      error: payload,
      loading: false,
      isFailedLogin: true,
      action: type,
    }),
    DEFAULT: () => state,
  };

  return (actions[type] || actions.DEFAULT)();
};

export default login;
