import React, { useEffect, useRef, useState } from 'react';
import { Table, Col, Row, Card, CardBody, CardImg } from 'reactstrap';
import MetaTags from 'react-meta-tags';
import 'react-responsive-carousel/lib/styles/carousel.min.css'; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import { useDispatch, useSelector } from 'react-redux';
import { fetchDataWO } from '@app-modules/dashboard/actions';
import './style.scss';
import _ from 'lodash';
import moment from 'moment';
import { convertToCapitalCase } from '@app-utils/Commons';
import { FETCH_LIST_ANTRIAN_WO_SUCCESS } from '@app-modules/dashboard/actionTypes';
import Footer from './footer';
function usePrevious(value) {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
}

const Dashboard = (props) => {
  const dispatch = useDispatch();
  const player = useRef(null);
  const [list, setList] = useState([]);
  const [ready, setReady] = useState(false);
  const [bannerPromo, setBannerPromo] = useState([]);
  const { wo, auth } = useSelector((state) => state);
  const action = wo.action;
  const {
    sessionUser: {
      data: { user_branch_id },
    },
  } = auth;
  const prevAction = usePrevious(wo);
  const limit = 6;

  const FetchData = () => {
    dispatch(
      fetchDataWO({
        startDate: moment().format('yyyy/MM/DD'),
        FromDate: moment().format('yyyy/MM/DD'),
      })
    );
  };

  useEffect(() => {
    const intervalId = setInterval(() => {
      FetchData();
      fetch('./promo.json')
        .then(function (res) {
          return res.json();
        })
        .then(function (data) {
          // store Data in State Data Variable
          setBannerPromo(data);
          console.log('data', data);
        })
        .catch(function (err) {
          console.log(err, ' error');
        });
    }, 2000 * 60); // in milliseconds
    return () => clearInterval(intervalId);
  }, [dispatch]);

  useEffect(() => {
    fetch('./promo.json')
      .then(function (res) {
        return res.json();
      })
      .then(function (data) {
        // store Data in State Data Variable
        setBannerPromo(data);
        console.log('data', data);
      })
      .catch(function (err) {
        console.log(err, ' error');
      });
    FetchData();
  }, [dispatch]);

  useEffect(() => {
    if (prevAction && prevAction !== action) {
      onActionChange(action);
    }
  }, [action]);

  useEffect(() => {
    setTimeout(() => {
      setReady(true);
    }, 5 * 1000);
  }, []);

  function onActionChange(action) {
    const actions = {
      [FETCH_LIST_ANTRIAN_WO_SUCCESS]: () => {
        setList(wo?.response?.listObj);
      },
      DEFAULT: () => {},
    };
    return (actions[action] || actions.DEFAULT)();
  }

  function showPlatNo(val) {
    if (val.length > 2) {
      let plat = val.substr(0, 2);
      const check = /^[a-zA-Z]*$/.test(plat);
      if (check) {
        return val.substr(0, 2) + '-' + val.substr(2, 4) + '-' + val.substr(6);
      } else {
        return val[0] + '-' + val.substr(1, 4) + '-' + val.substr(5);
      }
    }
    return val;
  }
  const itemList = (index, data) => {
    return (
      <div key={index}>
        <Table className="table table-striped mb-0">
          <thead className="table-light">
            <tr>
              <th>NO</th>
              <th>NAMA</th>
              <th>NOPOL</th>
              <th>STATUS</th>
            </tr>
          </thead>
          <tbody>
            {data.map((data, _) => {
              const i =
                index === 0 ? index * limit + _ + 1 : index * limit + _ + 1;
              let status = 'ANTRIAN';
              let color = 'bg-green';
              switch (data.status) {
                case 'OPEN':
                  status = 'ANTRIAN';
                  color = 'bg-primary text-white';
                  break;
                case 'PROSES':
                  status = 'SEDANG SERVIS';
                  color = 'bg-warning text-dark';
                  break;
                case 'CLOSE':
                  status = 'SELESAI';
                  color = 'bg-success text-dark';
                  break;
                case 'FINISH':
                  status = 'SELESAI';
                  color = 'bg-success text-dark';
                  break;
                default:
                  status = data.status;
                  color = 'bg-success text-dark';
                  break;
              }
              return (
                <tr className="table-light">
                  <th scope="row">{i}</th>
                  <td>{convertToCapitalCase(data.wO_Cont_Name, false)}</td>
                  <td>{showPlatNo(data.wo_Registration_number)}</td>
                  <td>{status}</td>
                </tr>
              );
            })}
          </tbody>
        </Table>
      </div>
    );
  };

  const renderList = () => {
    if (_.size(list) > 1) {
      const dataTemp = _.chunk(list, limit);
      return dataTemp.map((data, index) => {
        return itemList(index, data);
      });
    }
    return <div>Belum ada data</div>;
  };

  const filterBanner = _(bannerPromo)
    .filter((z) => z.branchId === user_branch_id)
    .head();

  const renderHeader = () => {
    return (
      <Card>
        <CardBody>
          <nav>
            <Col lg={12}>
              <Card>
                <CardImg
                  className="img-fluid"
                  src={
                    filterBanner?.topBanner ??
                    'https://i.ibb.co/6WvzFq8/banner-cibinong-top.png'
                  }
                  alt="bintang"
                />
              </Card>
            </Col>
          </nav>
        </CardBody>
      </Card>
    );
  };

  return (
    <React.Fragment>
      <MetaTags>
        <title>Dashboard</title>
      </MetaTags>
      <Row>
        <Col xl="8">
          <Row>
            {renderHeader()}
            <Col xl="12">
              {ready ? (
                <Carousel
                  showArrows={false}
                  showThumbs={false}
                  dynamicHeight={false}
                  autoPlay
                  interval={10000}
                  infiniteLoop
                  stopOnHover={false}
                  // axis="vertical"
                >
                  {renderList()}
                </Carousel>
              ) : null}
            </Col>
          </Row>
        </Col>
        {/* <!--footer --> */}
        <Col xl="4">
          <Footer filterBanner={filterBanner} />
        </Col>
      </Row>
    </React.Fragment>
  );
};
export default Dashboard;
