import * as TYPES from './actionTypes';

export const fetchDataWO = (payload) => {
  return {
    type: TYPES.FETCH_LIST_ANTRIAN_WO,
    payload,
  };
};

export const fetchDataWOSuccess = (payload) => {
  return {
    type: TYPES.FETCH_LIST_ANTRIAN_WO_SUCCESS,
    payload,
  };
};

export const fetchDataWOFailed = (payload) => {
  return {
    type: TYPES.FETCH_LIST_ANTRIAN_WO_FAILED,
    payload,
  };
};

export const fetchDataWOReset = () => {
  return {
    type: TYPES.FETCH_LIST_ANTRIAN_WO_RESET,
  };
};
export const fetchDataWOClear = () => {
  return {
    type: TYPES.FETCH_LIST_ANTRIAN_WO_CLEAR,
  };
};
