import { createStore, applyMiddleware, compose } from 'redux';
import { persistReducer, persistStore } from 'redux-persist';
import bootstrapReducers from '@app-bootstrap/bootstrapReducers';
import Persist from './Persist';
import middleware, { sagaMiddleware } from './Middleware';

let finalReducers = bootstrapReducers;
if (Persist.active) {
  const persistConfig = Persist.storeConfig;
  finalReducers = persistReducer(persistConfig, bootstrapReducers);
}

const composeEnhancer =
  (process.env.NODE_ENV !== 'production'
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    : null) || compose;
export const store = createStore(
  finalReducers,
  composeEnhancer(applyMiddleware(sagaMiddleware, ...middleware))
);
export const persistor = persistStore(store);
