import { httpClient, post } from '@app-utils/httpClient';

export const fetchListAntrianWoServices = (param) => {
  httpClient.defaults.headers.common = {
    Authorization: `Bearer ${param.token}`,
  };
  return post('/workorder?pagesize=250', {
    startDate: param.startDate,
    FromDate: param.FromDate,
    operation: 'branchId',
  });
};
