import { put, call, takeLatest, select, cancelled } from 'redux-saga/effects';
import { notification } from '@app-utils/NotificationToast';
// Login Redux States
import * as TYPES from './actionTypes';
import {
  fetchDataWOSuccess,
  fetchDataWOFailed,
  fetchDataWOReset,
} from './actions';
import { RESPONSE_STATUS } from '@app-utils/Constants';
import { fetchListAntrianWoServices } from './services';

function* listAntrianWo({ payload: { startDate, FromDate } }) {
  try {
    const token = yield select(
      (state) => state.auth.sessionUser.data.user_token
    );
    const response = yield call(fetchListAntrianWoServices, {
      startDate,
      FromDate,
      token,
    });
    switch (response.status.code) {
      case RESPONSE_STATUS.SUCCESS:
        yield put(fetchDataWOSuccess(response));
        break;
      case RESPONSE_STATUS.BAD_REQUEST:
        yield put(fetchDataWOFailed(response));
        break;
      case RESPONSE_STATUS.NOT_FOUND:
        yield put(fetchDataWOFailed(response));
        break;
      case RESPONSE_STATUS.ERROR:
        yield put(fetchDataWOFailed(response));
        break;
      default:
        break;
    }
  } catch (error) {
    notification({
      type: 'error',
      text: error.message,
    });
    yield put(fetchDataWOFailed({ error: error.message }));
  } finally {
    yield put(fetchDataWOReset());
  }
}

const wo = [takeLatest(TYPES.FETCH_LIST_ANTRIAN_WO, listAntrianWo)];

export default wo;
