import * as TYPES from './actionTypes';
import { cloneDeep } from 'lodash';

const initialState = {
  loading: false,
  type: '',
  status: 'iddle',
  response: {},
  responseError: {},
  responseParam: {},
  action: '',
};

const listWorkOrder = (state = cloneDeep(initialState), action) => {
  const { payload, type } = action;
  const actions = {
    [TYPES.FETCH_LIST_ANTRIAN_WO_RESET]: () => ({
      ...state,
      loading: false,
      action: type,
      status: 'iddle',
    }),
    [TYPES.FETCH_LIST_ANTRIAN_WO]: () => ({
      ...state,
      loading: true,
      action: type,
      responseParam: payload,
      status: 'fetch',
    }),
    [TYPES.FETCH_LIST_ANTRIAN_WO_SUCCESS]: () => ({
      ...state,
      loading: true,
      action: type,
      response: payload,
      status: 'success',
    }),
    [TYPES.FETCH_LIST_ANTRIAN_WO_FAILED]: () => ({
      ...state,
      loading: true,
      action: type,
      responseError: payload,
      status: 'failed',
    }),
    [TYPES.FETCH_LIST_ANTRIAN_WO_CLEAR]: () => ({
      ...state,
      ...cloneDeep(initialState),
      action: type,
    }),
    DEFAULT: () => state,
  };

  return (actions[type] || actions.DEFAULT)();
};

export default listWorkOrder;
