import React, { Suspense, lazy } from 'react';
import { connect } from 'react-redux';
import { Switch, Route, Redirect, useLocation } from 'react-router-dom';
import { AnimatePresence, motion } from 'framer-motion';
import { ClimbingBoxLoader } from 'react-spinners';
import PresentationLayout from '@app-middleware/presentationLayout';
import HorizontalLayout from '@app-components/HorizontalLayout';
import RoutesPath from './RoutesPath.json';
import DashboardRoutes from './dashboard';

const AppRoutes = (props) => {
  const location = useLocation();

  const pageVariants = {
    initial: {
      opacity: 0,
      scale: 0.99,
    },
    in: {
      opacity: 1,
      scale: 1,
    },
    out: {
      opacity: 0,
      scale: 1.01,
    },
  };

  const pageTransition = {
    type: 'tween',
    ease: 'anticipate',
    duration: 0.4,
  };

  const SuspenseLoading = () => {
    return (
      <>
        <div className="d-flex align-items-center flex-column vh-100 justify-content-center text-center py-3">
          <div className="d-flex align-items-center flex-column px-4">
            <ClimbingBoxLoader color={'#3c44b1'} loading={true} />
          </div>
          <div className="text-muted font-size-xl text-center pt-3">
            Sedang memuat ...
          </div>
        </div>
      </>
    );
  };

  return (
    <AnimatePresence>
      <Suspense fallback={<SuspenseLoading />}>
        <Switch>
          <Redirect exact from="/" to="/signin" />
          <Route path={['/signin']}>
            <PresentationLayout>
              <Switch location={location} key={location.pathname}>
                <motion.div
                  initial="initial"
                  animate="in"
                  exit="out"
                  variants={pageVariants}
                  transition={pageTransition}>
                  <Route
                    path="/signin"
                    component={lazy(() =>
                      import('@app-modules-sign/pages/Login')
                    )}
                  />
                </motion.div>
              </Switch>
            </PresentationLayout>
          </Route>
          <Route path={['/logout']}>
            <PresentationLayout>
              <Switch location={location} key={location.pathname}>
                <motion.div
                  initial="initial"
                  animate="in"
                  exit="out"
                  variants={pageVariants}
                  transition={pageTransition}>
                  <Route
                    path="/logout"
                    component={lazy(() =>
                      import('@app-modules-sign/pages/Logout')
                    )}
                  />
                </motion.div>
              </Switch>
            </PresentationLayout>
          </Route>
          <Route path={RoutesPath}>
            <HorizontalLayout>
              <Switch location={location} key={location.pathname}>
                <motion.div
                  initial="initial"
                  animate="in"
                  exit="out"
                  variants={pageVariants}
                  transition={pageTransition}>
                  <DashboardRoutes />
                </motion.div>
              </Switch>
            </HorizontalLayout>
          </Route>
        </Switch>
      </Suspense>
    </AnimatePresence>
  );
};

// export default AppRoutes;
const mapStateToProps = (state) => ({
  //   auth: state.session,
});

export default connect(mapStateToProps, null)(AppRoutes);
