import React, { useEffect, useRef, useState } from 'react';
import { Card, CardBody } from 'reactstrap';
import SimpleBar from 'simplebar-react';
import ReactPlayer from 'react-player';
import 'react-responsive-carousel/lib/styles/carousel.min.css'; // requires a loader
import { Carousel } from 'react-responsive-carousel';

function Footer(props) {
  const { filterBanner } = props;
  const player = useRef(null);

  const listFooter = () => {
    return filterBanner?.banner.map((data, i) => {
      return (
        <div className="rounded" key={i}>
          <img
            src={
              data?.url ??
              'https://i.ibb.co/cJgcBSH/Whats-App-Image-2021-06-08-at-11-58-40.jpg'
            }
          />
        </div>
      );
    });
  };

  return (
    <Card>
      <CardBody>
        <div className="ratio ratio-4x3 rounded">
          <ReactPlayer
            ref={player}
            playing
            loop
            url={
              filterBanner?.youtube ??
              'https://www.youtube.com/watch?v=sJ8WyIzzw1c'
            }
            width="100%"
            height="100%"
            className="ratio ratio-4x3 rounded"
          />
        </div>
        <div className="banner rounded">
          <SimpleBar style={{ maxHeight: '500px' }}>
            <Carousel
              showArrows={true}
              showThumbs={false}
              dynamicHeight={false}
              autoPlay
              interval={15000}
              infiniteLoop
              stopOnHover={false}
              axis="vertical">
              {listFooter()}
            </Carousel>
          </SimpleBar>
        </div>
      </CardBody>
    </Card>
  );
}

export default Footer;
