import React, { useEffect, useState } from 'react';
import { Container, Row, Col } from 'reactstrap';
import Marquee from 'react-fast-marquee';
import './style.scss';
import { useSelector } from 'react-redux';
import _ from 'lodash';
const Footer = () => {
  const [time, setTime] = React.useState(0);
  const [timeamPm, setTimeamPm] = React.useState(0);
  const [bannerPromo, setBannerPromo] = useState([]);
  const { wo, auth } = useSelector((state) => state);
  const {
    sessionUser: {
      data: { user_branch_id },
    },
  } = auth;

  useEffect(() => {
    fetch('./promo.json')
      .then(function (res) {
        return res.json();
      })
      .then(function (data) {
        // store Data in State Data Variable
        setBannerPromo(data);
        console.log('data', data);
      })
      .catch(function (err) {
        console.log(err, ' error');
      });
  }, []);

  useEffect(() => {
    const intervalId = setInterval(() => {
      fetch('./promo.json')
        .then(function (res) {
          return res.json();
        })
        .then(function (data) {
          // store Data in State Data Variable
          setBannerPromo(data);
          console.log('data', data);
        })
        .catch(function (err) {
          console.log(err, ' error');
        });
    }, 2000 * 60); // in milliseconds
    return () => clearInterval(intervalId);
  }, []);

  const filterBanner = _(bannerPromo)
    .filter((z) => z.branchId === user_branch_id)
    .head();

  const getTime = () => {
    const takeTwelve = (n) => (n > 12 ? n - 12 : n),
      addZero = (n) => (n < 10 ? '0' + n : n);

    setInterval(() => {
      let d, h, m, s, t, amPm;

      d = new Date();
      h = addZero(takeTwelve(d.getHours()));
      m = addZero(d.getMinutes());
      s = addZero(d.getSeconds());
      t = `${h}:${m}:${s}`;

      amPm = d.getHours() >= 12 ? 'pm' : 'am';
      setTime(t);
      setTimeamPm(amPm);
    }, 1000);
  };

  getTime();

  return (
    <React.Fragment>
      <footer className="footer footer-color">
        <Row>
          <Col md={12}>
            <Marquee gradient={false}>
              <div
                style={{
                  fontSize: 30,
                  fontWeight: 'bold',
                  padding: '5px',
                  color: '#FFFFFF',
                }}>
                {filterBanner?.textPromo}
              </div>
            </Marquee>

            <div className="most-inner">
              <span className={time === '00:00:00' ? 'time blink' : 'time'}>
                {' '}
                {time}
              </span>
            </div>
          </Col>
        </Row>
      </footer>
    </React.Fragment>
  );
};

export default Footer;
